// Nuxeo Base styling
@import 'settings';

// Nuxeo Mixins
@import 'nuxeo-mixins';

// @import 'Light';
// @import 'LightItalic';
@import 'Regular';
@import 'Italic';
@import 'Semibold';
@import 'SemiboldItalic';
@import 'Bold';
@import 'BoldItalic';
@import 'ExtraBold';
@import 'ExtraBoldItalic';

// Foundation base
@import 'foundation';

// Optional components that can be included outside of nuxeo-base-styles
@import 'components/codeblock';

@mixin nuxeo-base-styles {
  // We could include settings and the base Foundation styles here
  * {
    font-family: $nuxeo-font-family;
  }
}
